.menu {
  display: table;
  margin: 2rem auto 0;
  font-size: 16px;

  &__heading {
    margin: 0;
    padding: 2rem 0;
    font-size: 34px;
    line-height: 3rem;
    text-transform: uppercase;
  }

  @width: 12em;
  @height: 12em;

  &__list {
    display: grid;
    grid-template-columns: @width @width @width @width @width @width @width;
    grid-gap: 2em;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
  }

  &__txt {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    line-height: 1rem;
  }

  &__img {
    position: relative;
    height: @height;
    width: @width;
    flex-shrink: 0;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      border-radius: 0.25rem;
    }
  }

  &__title {
    color: #2F191B;
    margin-top: 1rem;
    margin-bottom: 0;
    font-size: 16px;
    height: 2rem;
    // text-transform: uppercase;
  }

  &__desc {
    color: black;
    margin-top: 0.5rem;
    font-size: 14px;
    flex-grow: 1;
  }

  &__price {
    margin-top: 1rem;
    padding: 0.25rem;
    color: white;
    font-weight: bold;
    text-align: center;
    background-color: #2F191B;
    border-radius: 0.25rem;
  }

  &__features-list {
    position: absolute;
    top: -1rem;
    right: -1rem;
    display: flex;
  }

  &__features-item {
    font-size: 0;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    z-index: 1;

    svg {
      position: relative;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      filter: invert(1);
    }
  }

  &__features-item ~ &__features-item {
    margin-left: -1.5rem;
  }

  &__features-item--beef {
    background-color: #2F191B;
  }

  &__features-item--chicken {
    background-color: #E8D4A3;
    transform: scale(-1, 1);

    svg {
      filter: none;
    }
  }

  &__features-item--pork {
    background-color: #52221F;
  }

  &__features-item--pepper {
    background-color: #931C19;

    svg {
      width: 55%;
    }
  }

  &__features-item--sausages {
    background-color: #E2A946;

    svg {
      filter: none;
    }
  }



  &__list--horizontal {
    grid-template-columns: @width*2 @width*2 @width*2;
    grid-gap: 3em;
  }

  &__list--horizontal &__item {
    flex-direction: row;
  }

  &__list--horizontal &__img {
    width: 0.75 * @width;
    height: 0.75 * @height;
  }

  &__list--horizontal &__txt {
    padding-left: 1rem;
  }

  &__list--horizontal &__title {
    margin-top: 0;
  }

  &__list--horizontal &__features-list {
    right: auto;
    left: -1rem;
  }
}
